var code7=`#! /usr/bin/env python
"""
    
    :
        1. /spawn
        2. turtlesim/Spawn
        3. turtlesim_node 

    :
        1.
           turtlesim  package.xml 
        2. ros 
        3. service 
        4.
        5.
        6.

"""

import rospy
from turtlesim.srv import Spawn,SpawnRequest,SpawnResponse

if __name__ == "__main__":
    # 2. ros 
    rospy.init_node("service_call_p")
    # 3. service 
    client = rospy.ServiceProxy("/spawn",Spawn)
    # 4.
    client.wait_for_service()
    # 5.
    req = SpawnRequest()
    req.x = 2.0
    req.y = 2.0
    req.theta = -1.57
    req.name = "turtle3"
    try:
        response = client.call(req)
        # 6.
        rospy.loginfo("!:%s",response.name)
    except Exception as e:
        rospy.loginfo("")`
