  var code=`#include<string.h>
#include<stdio.h>
#define N 20
int n;
char g[N][N];
int col[N],dg[N],udg[N];
void dfs(int x){
    if(x==n){
        for(int i = 0 ; i < n ; i ++)puts(g[i]);
        puts("");
        return;
    }
    for(int i = 0 ; i < n ; i ++){
        if(!col[i]&&!dg[x+i]&&!udg[n-x+i]){
            g[x][i]='Q';
            col[i]=dg[x+i]=udg[n-x+i]=1;
            dfs(x+1);
            col[i]=dg[x+i]=udg[n-x+i]=0;
            g[x][i]='.';
        }
    }
}
int main(){
    scanf("%d",&n);
    for(int i = 0 ; i < n ; i++){
        for(int j = 0 ; j < n ; j ++){
            g[i][j]='.';
        
        }
    }
    dfs(0);
    return 0;
}

