/*
     ros::NodeHandle
        param(,) 
            

        getParam(,)
            , true,2
             false2

        getParamCached,)--
            , true,2
             false2

        getParamNames(std::vector<std::string>)
            , vector  

        hasParam()
             true false

        searchParam(12)
            12

    ros::param -----  NodeHandle 
 */
int main(int argc, char *argv[])
{
    setlocale(LC_ALL,"");
    ros::init(argc,argv,"get_param_c");
    ros::NodeHandle nh;
    //roshandle,param------------------------
    //1param
    double radius=nh.param("radius",0.5);
    ROS_INFO("radius=%.2f",radius);
    //2getparam
    double radius2=0.0;
    bool result = nh.getParam("radiusxxxx",radius2);
    if(result){
        ROS_INFO("%.2f",radius2);
    }else{
        ROS_INFO("");
    }
    //3getParamNames
    std::vector<std::string>names;
    nh.getParamNames(names);
    for(auto &&name:names){
        ROS_INFO("%s",name.c_str());
    }
    //4hasParam
    bool flag1=nh.hasParam("radius");
    bool flag2=nh.hasParam("radiusxxx");
    ROS_INFO("radius  %d",flag1);
    ROS_INFO("radiusxxx  %d",flag2);
    //5searchParam
    std::string key;
    nh.searchParam("radius",key);
    ROS_INFO("%s",key.c_str());
    return 0;
}
