var code9=`
#include "ros/ros.h"
#include "sensor_msgs/Imu.h"
#include "tf/tf.h"
#include "geometry_msgs/Twist.h"

// 
ros::Publisher vel_pub;

// IMU 
void IMUCallback(const sensor_msgs::Imu msg)
{
    // 
    if(msg.orientation_covariance[0] < 0)
        return;
    // 
    tf::Quaternion quaternion(
        msg.orientation.x,
        msg.orientation.y,
        msg.orientation.z,
        msg.orientation.w
    );
    double roll, pitch, yaw;
    tf::Matrix3x3(quaternion).getRPY(roll, pitch, yaw);
    // 
    roll = roll*180/M_PI;
    pitch = pitch*180/M_PI;
    yaw = yaw*180/M_PI;
    ROS_INFO("= %.0f = %.0f = %.0f", roll, pitch, yaw);
    // 
    geometry_msgs::Twist vel_cmd;
    // 
    double target_yaw = 90;
    // 
    double diff_angle = target_yaw - yaw;
    vel_cmd.angular.z = diff_angle * 0.01;
    vel_cmd.linear.x = 0.1;
    vel_pub.publish(vel_cmd);
}

int main(int argc, char **argv)
{
    setlocale(LC_ALL, "");
    ros::init(argc,argv, "demo_imu_behavior"); 

    ros::NodeHandle n;
    //  IMU 
    ros::Subscriber sub = n.subscribe("imu/data", 100, IMUCallback);
    // 
    vel_pub = n.advertise<geometry_msgs::Twist>("/cmd_vel",10);
    ros::spin();

    return 0;
}
`
var code10=`#!/usr/bin/env python3
# coding=utf-8

import rospy
from sensor_msgs.msg import Imu
from tf.transformations import euler_from_quaternion
import math
from geometry_msgs.msg import Twist

# IMU 
def imu_callback(msg):
    if msg.orientation_covariance[0] < 0:
        return
    # 
    quaternion = [
        msg.orientation.x,
        msg.orientation.y,
        msg.orientation.z,
        msg.orientation.w
    ]
    (roll,pitch,yaw) = euler_from_quaternion(quaternion)
    # 
    roll = roll*180/math.pi
    pitch = pitch*180/math.pi
    yaw = yaw*180/math.pi
    rospy.loginfo("= %.0f = %.0f = %.0f", roll, pitch, yaw)
    # 
    vel_cmd = Twist()
    # 
    target_yaw = 90
    # 
    diff_angle = target_yaw - yaw
    vel_cmd.angular.z = diff_angle * 0.01
    vel_cmd.linear.x = 0.1
    global vel_pub
    vel_pub.publish(vel_cmd)

# 
if __name__ == "__main__":
    rospy.init_node("imu_behavior")
    #  IMU 
    imu_sub = rospy.Subscriber("/imu/data",Imu,imu_callback,queue_size=10)
    # 
    vel_pub = rospy.Publisher("cmd_vel",Twist,queue_size=10)
    rospy.spin()`
