    var val5='bin/hbase shell'
    var val6='exit'
    var val7='bin/stop-hbase.sh'
    var val8='cd /usr/local/hadoop'
    var val9='./sbin/stop-dfs.sh'
    var val10=`create 'student','Sname','Ssex','Sage','Sdept','course'`
