    <script setup>
    var code=`// SearchLeft SL
int SL(int l, int r)
{
    while (l < r)
    {
        int mid = l + r >> 1;
        if (check(mid)) r = mid; 
        else l = mid + 1; 
    }   
    return l;
}
// SearchRight SR 
int SR(int l, int r) 
{
    while (l < r)
    {                   
        int mid = l + r + 1 >> 1; //+1 
        if (check(mid)) l = mid;
        else r = mid - 1; 
    }
    return r; 
}`
